.form {
  font-size: 16px;
  font-family: $ff-oswald;
  color: $secondary-dark-grey;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.form__container {
  @media (min-width: $tablet-width) {
    padding: 0 70px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.form__fieldset-outer {
  @media (min-width: $desktop-width) {
    display: grid;

    gap: 80px;
    grid-template-columns: 1fr 1fr;
  }
}

.form__fieldset {
  display: grid;
  margin-bottom: 24px;
  padding: 0;

  border: none;

  gap: 14px;
  grid-template-columns: 1fr;

  @media (min-width: $tablet-width) {
    gap: 20px;
  }
}

.form__fieldset--radio {
  margin: 0 -20px 26px;
  padding: 22px 20px 19px;

  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    margin: 0 0 26px;
    padding: 23px 27px 27px;

    border: 1px solid #d9d9d9;
  }
}

.form__control {
  &:hover,
  &:active {
    border-color: $control;
  }

  &:focus,
  &:focus-visible {
    outline: none;
    opacity: 0.3;
  }

  &:disabled,
  &:disabled + .form__control-text {
    opacity: 0.3;

    pointer-events: none;
  }
}

.form__control--radio {
  position: relative;

  width: 30px;
  height: 30px;
  margin: 0;

  border: 1px solid $line-grey;
  border-radius: 50%;

  appearance: none;

  &:checked::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 99;

    width: 13px;
    height: 13px;

    background-color: $basic-green;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;

    &:checked::after {
      width: 19px;
      height: 19px;
    }
  }
}

.form__radio-item {
  @media (min-width: $tablet-width) {
    gap: 20px;
  }
}

.form__control-label {
  display: grid;
  align-items: center;

  gap: 15px;
  grid-template-columns: auto 1fr;
}

.form__control-item {
  margin-bottom: 13px;
}

.form__control--check {
  position: relative;

  width: 26px;
  height: 26px;
  margin: 0;

  border: 1px solid $line-grey;

  appearance: none;

  &:checked {
    background-image: url("../img/svg/icon-checked.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 18px 14px;
  }

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;

    &:checked {
      background-size: 26px 24px;
    }
  }
}

.form__fieldset--control {
  @media (min-width: $tablet-width) {
    margin-bottom: 52px;
  }
}

.form__control-outer {
  display: grid;

  @media (min-width: $tablet-width) {
    margin-bottom: 52px;

    gap: 32px;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
  }
}

.form__fieldset--button {
  @media (min-width: $tablet-width) {
    align-items: center;

    gap: 32px;
    grid-template-columns: 1fr 1fr;
  }
}

.form__item-outer {
  display: grid;

  gap: 14px;
  grid-template-columns: 1fr;

  @media (min-width: $tablet-width) {
    gap: 20px;
  }

  @media (min-width: $desktop-width) {
    gap: 80px;
    grid-template-columns: 1fr 1fr;
  }
}

.form__item {
  display: grid;
  align-items: baseline;

  gap: 15px;
  grid-template-columns: auto 1fr;
}

.form .form__title {
  margin-bottom: 35px;

  font-weight: 400;
  color: $dark;
  text-transform: none;

  @media (min-width: $tablet-width) {
    margin-bottom: 75px;
  }
}

.form__input-outer {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 52%;
    right: 12px;
    z-index: 99;

    width: 20px;
    height: 20px;

    background-color: $dark;
    transform: translateY(-52%);

    mask-repeat: no-repeat;
  }

  &:focus-within::after {
    background-color: $basic-green;
  }

  &:hover::after {
    background-color: $light-black;
  }
}

