.form__input--email::after {
  mask-image: url("../img/svg/icon-email.svg");
}

.form__input--phone::after {
  mask-image: url("../img/svg/icon-phone.svg");
}

.form__input {
  width: 100%;
  padding: 14px 35px 13px 15px;

  border: 1px solid $special-line-grey;

  &:hover {
    border-color: $line-grey;
  }

  &:focus,
  &:focus-visible {
    border-color: 1px solid $secondary-dark-grey;
    outline: none;
    box-shadow: 0 0 0 1px $secondary-dark-grey;
  }

  &:invalid {
    border-color: $error;
    box-shadow: 0 0 0 1px $error;
  }

  // &:valid {
  //   border-color: $basic-green;
  //   box-shadow: 0 0 0 1px $basic-green;
  // }
}

