.form__input::placeholder {
  font-size: 16px;
  font-family: $ff-oswald;
  color: $secondary-dark-grey;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.form__textarea::placeholder {
  font-size: 16px;
  font-family: $ff-oswald;
  color: $secondary-dark-grey;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.form__textarea {
  width: 100%;
  height: 150px;
  padding: 14px 15px 13px;

  border: 1px solid $special-line-grey;

  resize: none;

  &:focus,
  &:focus-visible {
    border-color: $secondary-dark-grey;
    box-shadow: 0 0 0 1px $secondary-dark-grey;
  }
}

.form__button {
  display: block;

  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  background-color: $basic-green;
  border: none;

  @media (min-width: $tablet-width) {
    width: 284px;
    padding: 14px 0;

    list-style: 26px;
    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
  }
}

.form__important {
  font-size: 14px;
  line-height: 16px;
  font-family: $ff-lato;
  text-align: center;
  text-transform: initial;

  @media (min-width: $tablet-width) {
    text-align: left;
  }
}
